<template>
  <div class="hello">
    <h1>svg测试c</h1>
    <div id="boxContainer" style="position: relative;width: 300px;height: 300px;">
      <!--<svg id="box" width="300" height="300">-->
      <!--&lt;!&ndash;<polyline points='220,10 300,210 170,250 123,234' fill='none' stroke='#000' stroke-width='1'></polyline>&ndash;&gt;-->
      <!--<text x="250" y="250" fill="red" text-anchor="start" dominant-baseline="hanging">I study SVG</text>-->

      <!--</svg>-->
      <div id="drawing"></div>
      <!--<img src="../../static/33.jpeg" style="width: 500px;height: 500px;position: absolute;top: 0;z-index: -1;display: block;left: 0;" alt="">-->
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  import svg from 'svg.js'

  export default {
    name: 'HelloWorld',
    data() {
      return {
        msg: 'Welcome to Your Vue.js App',
        resArr: [],
        arr1: [],
        arr2: []
      }
    },
    methods: {
      svg_data() {
        var draw = SVG('drawing').size(500, 500)
        $.ajax({
          type: 'post',
          url: 'http://localhost:3000/users/svgpost',
          success: function (data) {
            var polygon_data = JSON.parse(data[0].obj)
            polygon_data.forEach(function (ele, i) {
              draw.polygon(ele.arr).fill('#F6FFF1').stroke({width: 1, color: '#FF2E1B'})
            })
            console.log($('#SvgjsSvg1001 polygon'))
            $('#SvgjsSvg1001 polygon').click(function () {
              alert($(this).attr('stroke'))
            })
          }
        })
      }
    },
    mounted() {
      var that = this
      this.svg_data()


    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  * {
    margin: 0;
    padding: 0
  }

  svg {
    border: 1px seagreen solid;
    position: relative;
  }

  ul li:hover {
    color: blue
  }
</style>



